const eventManager = (() => {
  let camera = null
  let renderer = null
  let activeTargets = []
  let action = () => { }
  let userInteractionStartTime
  let selectedObj;

  const userInteractionVelocity = new THREE.Vector2()
  const userControlsPosition = new THREE.Vector2()
  const userInteractionInitialPosition = new THREE.Vector2()
  const raycaster = new THREE.Raycaster()

  const onWindowResize = () => {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
  }

  const calculateUserInteractionVelocity = () => {
    if (userInteractionStartTime) {
      const elapsed = (new Date().getTime() - userInteractionStartTime) / 100
      userInteractionVelocity.x = (userInteractionInitialPosition.x - userControlsPosition.x) / elapsed
      userInteractionVelocity.y = (userInteractionInitialPosition.y - userControlsPosition.y) / elapsed
      // console.log(userInteractionVelocity.x, userInteractionVelocity.y)
    } else {
      userInteractionVelocity.x = 0
      userInteractionVelocity.y = 0
    }
  }

  const consoleLog = () => {
    document.getElementById('x').innerHTML = userControlsPosition.x;
    document.getElementById('y').innerHTML = userControlsPosition.y;
    document.getElementById('intersect').innerHTML = selectedObj ? selectedObj.name : 'none';
  }

  const computeRaycasterIntersection = () => {
    raycaster.setFromCamera(userControlsPosition, camera);
    for (var at = 0; at < activeTargets.length; at++) {
      const box = activeTargets[at]
      if (raycaster.ray.intersectsBox(box.box)) {
        return box;
      }
    }
    return null;
  }

  const onMouseMove = event => {
    userControlsPosition.x = (event.clientX / window.innerWidth) * 2 - 1;
    userControlsPosition.y = - (event.clientY / window.innerHeight) * 2 + 1;
    if (userInteractionStartTime != null) calculateUserInteractionVelocity();
  }

  const onTouchMove = event => {
    userControlsPosition.x = +(event.targetTouches[0].pageX / window.innerWidth) * 2 - 1
    userControlsPosition.y = -(event.targetTouches[0].pageY / window.innerHeight) * 2 + 1
    if (userInteractionStartTime != null) calculateUserInteractionVelocity();
  }

  const onUserInteractionEnd = event => {
    event.preventDefault()
    raycaster.setFromCamera(userControlsPosition, camera);

    const intersection = computeRaycasterIntersection();
    const isClick = (new Date().getTime() - userInteractionStartTime) < 200;
    if (isClick && intersection === selectedObj) action(selectedObj, activeTargets.filter(x => x !== selectedObj))
    userInteractionStartTime = null
    selectedObj = null
  }

  const onMouseStart = event => {
    event.preventDefault()
    if (event.target != renderer.domElement) return
    userInteractionStartTime = new Date().getTime()
    userInteractionInitialPosition.x = (event.clientX / window.innerWidth) * 2 - 1;
    userInteractionInitialPosition.y = - (event.clientY / window.innerHeight) * 2 + 1;

    selectedObj = computeRaycasterIntersection()
  }

  const onTouchStart = event => {
    event.preventDefault()
    if (event.target != renderer.domElement) return
    userInteractionStartTime = new Date().getTime()
    userControlsPosition.x = +(event.targetTouches[0].pageX / window.innerWidth) * 2 - 1
    userControlsPosition.y = -(event.targetTouches[0].pageY / window.innerHeight) * 2 + 1
    userInteractionInitialPosition.x = +(event.targetTouches[0].pageX / window.innerWidth) * 2 - 1
    userInteractionInitialPosition.y = -(event.targetTouches[0].pageY / window.innerHeight) * 2 + 1

    selectedObj = computeRaycasterIntersection()
  }

  return {
    init: (initCamera, initRenderer, comm) => {
      camera = initCamera
      renderer = initRenderer
      window.addEventListener('mousedown', onMouseStart, false)
      window.addEventListener('mousemove', onMouseMove, false)
      window.addEventListener('mouseup', onUserInteractionEnd, false)
      renderer.domElement.addEventListener('touchstart', onTouchStart, false)
      renderer.domElement.addEventListener('touchmove', onTouchMove, false)
      renderer.domElement.addEventListener('touchend', onUserInteractionEnd, false)
      window.addEventListener('resize', onWindowResize, false);
      if (comm != null) {
        document.getElementsByName('tableParameter').forEach(a => {
          a.addEventListener('change', (b) => {
            comm.changeParameter('table', b.target.value)
          })
        })
        document.getElementsByName('bellParameter').forEach(a => {
          a.addEventListener('change', (b) => {
            comm.changeParameter('bell', b.target.value)
          })
        })
        document.getElementsByName('virtualObjParameter').forEach(a => {
          a.addEventListener('change', (b) => {
            comm.changeParameter('virtualObj', b.target.value)
          })
        })
      }
    },
    setActiveTargets: (targets, cb = () => { }) => {
      activeTargets = targets
      action = cb
    },
    setAction: cb => {
      action = cb
    },
    onSelect: cb => {

    },
    resetAction: () => {
      activeTargets = [];
      action = () => { }
    },
    getMouseVelocity: () => {
      if (userInteractionStartTime != null) return userInteractionVelocity
      userInteractionVelocity.x = Math.abs(userInteractionVelocity.x) > .0001 ? userInteractionVelocity.x * .95 : 0
      userInteractionVelocity.y = Math.abs(userInteractionVelocity.y) > .0001 ? userInteractionVelocity.y * .95 : 0
      return userInteractionVelocity
    },
    addEventListener: (objectId, cb) => {
      document.getElementById(objectId).addEventListener('click', cb)
    }
  }
})()

export default eventManager