const LinearNavigation = (() => {
  const moveFwd = (e, motion, move) => {
    e.preventDefault();
    e.stopPropagation();
    motion.moveForward = move;
  }

  const moveBwd = (e, motion, move) => {
    e.preventDefault();
    e.stopPropagation();
    motion.moveBackward = move;
  }

  const turn = (e, direction, motion, turn) => {
    e.preventDefault();
    e.stopPropagation();
    switch (direction) {
      case 'left':
        motion.moveLeft = turn;
        break;
      case 'right':
        motion.moveRight = turn;
        break;
    }
  }

  const eventListeners = []

  return {
    init: (motion) => {
      eventListeners.push(['moveForwardBtn', 'down', e => moveFwd(e, motion, true)])
      document.getElementById('moveForwardBtn').addEventListener('mousedown', eventListeners[0][2], false)
      document.getElementById('moveForwardBtn').addEventListener('touchstart', eventListeners[0][2], false)

      eventListeners.push(['moveForwardBtn', 'up', e => moveFwd(e, motion, false)])
      document.getElementById('moveForwardBtn').addEventListener('mouseup', eventListeners[1][2], false)
      document.getElementById('moveForwardBtn').addEventListener('touchend', eventListeners[1][2], false)

      eventListeners.push(['moveBackwardBtn', 'down', e => moveBwd(e, motion, true)])
      document.getElementById('moveBackwardBtn').addEventListener('mousedown', eventListeners[2][2], false)
      document.getElementById('moveBackwardBtn').addEventListener('touchstart', eventListeners[2][2], false)

      eventListeners.push(['moveBackwardBtn', 'up', e => moveBwd(e, motion, false)])
      document.getElementById('moveBackwardBtn').addEventListener('mouseup', eventListeners[3][2], false)
      document.getElementById('moveBackwardBtn').addEventListener('touchend', eventListeners[3][2], false)

      eventListeners.push(['moveLeftBtn', 'down', e => turn(e, 'left', motion, true)])
      document.getElementById('moveLeftBtn').addEventListener('mousedown', eventListeners[4][2], false)
      document.getElementById('moveLeftBtn').addEventListener('touchstart', eventListeners[4][2], false)

      eventListeners.push(['moveLeftBtn', 'up', e => turn(e, 'left', motion, false)])
      document.getElementById('moveLeftBtn').addEventListener('mouseup', eventListeners[5][2], false)
      document.getElementById('moveLeftBtn').addEventListener('touchend', eventListeners[5][2], false)

      eventListeners.push(['moveRightBtn', 'down', e => turn(e, 'right', motion, true)])
      document.getElementById('moveRightBtn').addEventListener('mousedown', eventListeners[6][2], false)
      document.getElementById('moveRightBtn').addEventListener('touchstart', eventListeners[6][2], false)

      eventListeners.push(['moveRightBtn', 'up', e => turn(e, 'right', motion, false)])
      document.getElementById('moveRightBtn').addEventListener('mouseup', eventListeners[7][2], false)
      document.getElementById('moveRightBtn').addEventListener('touchend', eventListeners[7][2], false)
    },
    reset: (motion) => {
      motion.moveForward = false;
      motion.moveBackward = false;
      motion.moveLeft = false;
      motion.moveRight = false;
      for (var i = 0; i < eventListeners.length; i++) {
        document.getElementById(eventListeners[i][0]).removeEventListener((eventListeners[i][1] == 'down' ? 'mousedown' : 'mouseup'), eventListeners[i][2], false)
        document.getElementById(eventListeners[i][0]).removeEventListener((eventListeners[i][1] == 'down' ? 'touchstart' : 'touchend'), eventListeners[i][2], false)
      }
    }
  }
})()

export default LinearNavigation;
