const assetLoader = (() => {
  THREE.DRACOLoader.setDecoderPath('/js/draco_decoder.js')
  // (Optional) Use JS decoder (defaults to WebAssembly if supported).
  THREE.DRACOLoader.setDecoderConfig({ type: 'js' });
  // Optional: Pre-fetch Draco WASM/JS module, to save time while parsing.
  THREE.DRACOLoader.getDecoderModule();

  const loadingManager = new THREE.LoadingManager()
  const objectLoader = new THREE.DRACOLoader(loadingManager)
  let totalItem = 0
  let totalData = 0
  let progressData = 0
  let isCompleted = false
  const assets = {}

  loadingManager.onStart = (url, itemsLoaded, itemsTotal) => {
    console.log(`Started loading assets`)
    isCompleted = false
    totalItem = itemsTotal
  }

  loadingManager.onProgress = (url, itemsLoaded, itemsTotal) => {
    console.log(`Asset complete: ${url} ${itemsLoaded} ${itemsTotal}`)
  }

  loadingManager.onLoad = () => {
    console.log('All assets downloaded')
    isCompleted = true
  }

  return {
    assets: {},
    total: 0,
    isCompleted: () => isCompleted,
    count: () => totalItem,
    progress: () => Math.round(progressData / totalData * 100),
    load: (assetName, assetUrl, callback) => {
      const onProgress = xhr => {
        if (xhr.lengthComputable) {
          if (typeof (assets[assetName]) === 'undefined') {
            totalData += xhr.total
          }
          assets[assetName] = xhr.loaded
          progressData = 0
          for (let i in assets) {
            progressData += assets[i]
          }
        }
      }
      const onError = e => console.log('error', e)

      objectLoader.load(assetUrl, callback, onProgress, onError);
    }
  }
})()

export default assetLoader