const appLoader = {
  load: (assetLoader, onLoad) => {
    const loaderModal = document.getElementById('loader-screen')
    const modalBody = loaderModal.getElementsByClassName("modal__body")[0]
    const progressBar = loaderModal.getElementsByClassName("progress-bar")[0]
    const progressBarIndicator = progressBar.getElementsByClassName("progress-bar__indicator")[0]

    modalBody.innerHTML = 'loading assets...'
    progressBar.style.opacity = 1

    const loader = setInterval(() => {
      if (assetLoader.isCompleted()) {
        clearInterval(loader)
        progressBarIndicator.style.width = '100%'
        loaderModal.classList.add('closed')
        modalBody.innerHTML = 'completed'
        setTimeout(() => {
          loaderModal.style.display = 'none'
        }, 3200)
        onLoad();
      } else {
        progressBarIndicator.style.width = assetLoader.progress() + "%"
      }
    }, 50)

  }
}

export default appLoader