import './scss/main.scss'
import assetLoader from './asset-loader.js'
import appLoader from './app-loader.js'
import eventManager from './events-manager.js'
import linearNavigation from './linear-navigation.js'
// import comm from './sockets.js'

let sceneCage
let table, tableBox
let bell, bellBox
let cage, cageBox
let lamp, lampBox
let virtualObj
let action = () => { }

const appContainer = document.getElementById("app-container")
const activeTargets = []
const interactiveObjects = []
const cameraTarget = new THREE.Vector3(0, 1.5, 0)
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 5000);
const motion = {
  moveForward: false,
  moveBackward: false,
  moveLeft: false,
  moveRight: false
}
const cages = []
const virtualObjects = []
const material = new THREE.MeshLambertMaterial();

camera.position.set(0, 1.5, -3)
camera.lookAt(cameraTarget);

const renderer = new THREE.WebGLRenderer({ alpha: true, antialias: true });
renderer.setSize(window.innerWidth, window.innerHeight);
appContainer.appendChild(renderer.domElement);

const ambientLight = new THREE.AmbientLight(0xcccccc, 0.4);
scene.add(ambientLight);
const lampLight = new THREE.PointLight(0xffffff, 0.8);
lampLight.position.y = 500;
scene.add(lampLight);

//const sphereSize = 1;
//const pointLightHelper = new THREE.PointLightHelper(lampLight, sphereSize);
//scene.add(pointLightHelper);

// const axesHelper = new THREE.AxesHelper(50);
// scene.add(axesHelper);

const groundGeometry = new THREE.PlaneGeometry(5000, 5000, 32);
const groundMaterial = new THREE.MeshBasicMaterial({ color: 0xdddddd, side: THREE.DoubleSide });
const ground = new THREE.Mesh(groundGeometry, groundMaterial);
ground.rotateX(Math.PI / 2)
scene.add(ground);

// setup fog
{
  const near = 10;
  const far = 30;
  const color = 0xb4b4b4;
  scene.fog = new THREE.Fog(color, near, far);
  scene.background = new THREE.Color(color);
}

// controls
const controls = new THREE.OrbitControls(camera, renderer.domElement)
controls.enableDamping = true
controls.dampingFactor = 0.15
controls.screenSpacePanning = false
controls.enablePan = false
controls.enableKeys = false
controls.minDistance = cameraTarget.distanceTo(camera.position)
controls.maxDistance = cameraTarget.distanceTo(camera.position)
controls.target.set(cameraTarget.x, cameraTarget.y, cameraTarget.z)
controls.update()
controls.maxPolarAngle = controls.getPolarAngle()
controls.minPolarAngle = controls.getPolarAngle()
controls.update()



const loadTable = (tableLoaded) => {
  table = new THREE.Mesh()
  table.geometry = tableLoaded
  table.material = material
  scene.add(table);
  interactiveObjects.push(table)
  tableBox = new THREE.Box3().setFromObject(table);
  activeTargets.push({ name: 'table', box: tableBox })
}

const loadBell = (bellLoaded) => {
  bell = new THREE.Mesh()
  bell.geometry = bellLoaded
  bell.material = material
  bell.scale.set(1.5, 1.5, 1.5)
  interactiveObjects.push(bell)
  bellBox = new THREE.Box3().setFromObject(bell)
  bell.position.y = 0.83 - bellBox.min.y
  scene.add(bell);
  activeTargets.push({ name: 'bell', box: bellBox.setFromObject(bell) })
}

const loadCage = (cageLoaded) => {
  lamp = new THREE.Mesh()
  lamp.geometry = cageLoaded
  lamp.material = material
  scene.add(lamp)
  cage = lamp.clone()
  const scaleMatrix = new THREE.Matrix4().makeScale(16, 16, 16)
  cage.applyMatrix(scaleMatrix)
  cage.rotateX(Math.PI)
  cageBox = new THREE.Box3().setFromObject(cage)
  cage.position.y = 80
  scene.add(cage)
  lamp.translateY(2.5)
  lamp.rotateX(Math.PI / 2)
  sceneCage = lamp
  lampBox = new THREE.Box3().setFromObject(lamp)
  activeTargets.push({ name: 'lamp', box: lampBox })
}

let dstDir
let cameraDirection = new THREE.Vector3()
let angleToDst
let rotationDirection


const exitCage = () => {
  // controls.enabled = false
  eventManager.resetAction();
  document.getElementById('exitCage').classList.remove('--d-block')

  document.getElementById('navigationControls').classList.add('--d-block');

  linearNavigation.init(motion);

  controls.enabled = false;

  action = () => {
    if (motion.moveForward) {
      camera.translateZ(-.2)
      camera.updateProjectionMatrix();

      cages.map((cage, index) => {
        const cageDistance = distanceVector(cage.position, camera.position)
        if (cageDistance < 20 && cageDistance > 5) {
          dstDir = new THREE.Vector3();
          dstDir.subVectors(cage.position, camera.position)
          dstDir.normalize()
          camera.getWorldDirection(cameraDirection)
          cameraDirection.normalize();
          angleToDst = cameraDirection.angleTo(dstDir)
          if (angleToDst > Math.PI / 3) return

          virtualObj = index < virtualObjects.length ? virtualObjects[index] : null

          const testDirection = cameraDirection.clone()
          testDirection.applyAxisAngle(camera.up, .01)
          const testAngleToDst = testDirection.angleTo(dstDir)
          console.log(angleToDst, testAngleToDst)
          rotationDirection = testAngleToDst < angleToDst ? 1 : -1

          linearNavigation.reset(motion);
          document.getElementById('navigationControls').classList.remove('--d-block');

          action = () => {
            if (angleToDst != 0) {
              angleToDst = cameraDirection.angleTo(dstDir)
              if (angleToDst > .3) {
                camera.rotateY(rotationDirection * .02)
                camera.getWorldDirection(cameraDirection)
              } else {
                camera.lookAt(cage.position.x, 1.5, cage.position.z)
                angleToDst = 0
              }
            }

            camera.updateProjectionMatrix();
            if (distanceVector(cage.position, camera.position) >= 3) {
              camera.translateZ(-.2)
              camera.updateProjectionMatrix();
              dstDir.subVectors(cage.position, camera.position)
            } else {
              controls.target.set(cage.position.x, 1.5, cage.position.z)
              controls.enabled = true;
              if (virtualObj == null)
                eventManager.setActiveTargets([{ name: 'table', box: tableBox }, { name: 'bell', box: bellBox.setFromObject(bell) }], interactiveObjectSelection)
              else
                eventManager.setActiveTargets([{ name: 'virtualObj', box: new THREE.Box3().setFromObject(virtualObj) }], interactiveObjectSelection)

              action = () => { }
            }
          }
        }
      })
    }

    if (motion.moveBackward) {
      camera.translateZ(.2)
      camera.updateProjectionMatrix();
    }

    if (motion.moveLeft) {
      camera.rotateY(.05)
      camera.updateProjectionMatrix();
    }

    if (motion.moveRight) {
      camera.rotateY(-.05)
      camera.updateProjectionMatrix();
    }
  }
}

const distanceVector = (v1, v2) => {
  var dx = v1.x - v2.x;
  var dz = v1.z - v2.z;

  return Math.sqrt(dx * dx + dz * dz);
}

let previousVelocity = 0

const interactiveObjectSelection = (selectedTarget, other) => {
  if (selectedTarget) {
    const { name } = selectedTarget
    // comm.lockResource(name);
    let notSelected, selected
    switch (name) {
      case 'bell':
        notSelected = [table]
        selected = bell
        break
      case 'table':
        notSelected = [bell]
        selected = table
        break
      case 'virtualObj':
        notSelected = []
        selected = virtualObj
    }
    for (let o = 0; o < other.length; o++) document.getElementById(`${other[o].name}-param-selector`).classList.remove('--show')
    document.getElementById(`${name}-param-selector`).classList.add('--show')
    document.getElementById('exitCage').classList.remove('--d-block')
    notSelected.map(ns => {
      ns.children.map(x => {
        x.material.transparent = true;
        x.material.opacity = .4
      })
    })

    selected.children.map(x => {
      x.material.opacity = 1
    })

    controls.enabled = false;

    action = () => {
      const userVelocity = parseFloat(eventManager.getMouseVelocity().x)
      let direction, rotation

      if (Math.abs(userVelocity) > .1) {
        direction = Math.abs(userVelocity) / userVelocity
        rotation = direction * .2
      } else {
        direction = 0
        rotation = userVelocity
      }

      if (rotation && !isNaN(rotation)) selected.rotateY(-rotation)
      if (direction != previousVelocity) {
        // comm.changeValue(name, direction)
        previousVelocity = direction
      }
    }
  }

  closeParameterMenu = () => {
    controls.enabled = true;
    // comm.rearNakedChoke();

    action = () => { }

    document.getElementById('exitCage').classList.add('--d-block')
    const menus = ['table', 'bell', 'virtualObj']
    for (let i = 0; i < menus.length; i++) {
      document.getElementById(`${menus[i]}-param-selector`).classList.remove('--show')
    }
    interactiveObjects.map(intObj => {
      intObj.children.map(x => {
        x.material.opacity = 1
      })
    })
  }
}

let closeParameterMenu = () => { }

const repositionCage = () => {
  cameraTarget.y += (sceneCage.position.y < cage.position.y) ? .1 : -.2
  if (cameraTarget.y > 1.5) {
    sceneCage.translateY(-.1)
    camera.lookAt(cameraTarget)
    if (cage.position.y > -cageBox.min.y) cage.translateY(.2)
  } else {
    scene.remove(sceneCage);
    sceneCage.children.map(mesh => {
      mesh.geometry.dispose();
    })
    cameraTarget.y = 1.5
    camera.lookAt(cameraTarget)
    eventManager.addEventListener('exitCage', () => exitCage())
    eventManager.addEventListener('closeParameterBell', () => closeParameterMenu())
    eventManager.addEventListener('closeParameterTable', () => closeParameterMenu())
    eventManager.addEventListener('closeParameterVirtual', () => closeParameterMenu())
    eventManager.setActiveTargets([{ name: 'table', box: tableBox }, { name: 'bell', box: bellBox.setFromObject(bell) }], interactiveObjectSelection)
    action = () => {
      if (scene.fog.far < 90) scene.fog.far += 2
      else {
        action = () => { }
        controls.enabled = true
      }
    }

    const r = 80
    const x = (r * Math.sqrt(2)) / 2
    const cagesPositions = [[0, r], [x, x], [r, 0], [-x, x], [-r, 0], [-x, -x], [0, -r], [x, -x]]
    const shapes = [
      THREE.DodecahedronGeometry.bind(this, .4),
      THREE.BoxGeometry.bind(this, .4, .4, .4),
      THREE.ConeGeometry.bind(this, .4, .4),
      THREE.CylinderGeometry.bind(this, .4, .4, .4),
      THREE.OctahedronGeometry.bind(this, .4),
      THREE.TorusGeometry.bind(this, .4, .2),
      THREE.IcosahedronGeometry.bind(this, .4),
      THREE.TetrahedronGeometry.bind(this, .4)]

    for (let i = 0; i < cagesPositions.length; i++) {
      const pos = cagesPositions[i]

      const geometry = new shapes[i]
      const material = new THREE.MeshPhongMaterial({ color: 0x666666 })
      const virtualObjMesh = new THREE.Mesh(geometry, material)
      virtualObjMesh.position.x = pos[0]
      virtualObjMesh.position.y = 1
      virtualObjMesh.position.z = pos[1]
      scene.add(virtualObjMesh)
      virtualObjects.push(virtualObjMesh)

      const newCage = cage.clone()
      newCage.position.x = pos[0]
      newCage.position.z = pos[1]
      newCage.rotateY(Math.random() * 360)
      scene.add(newCage)
      cages.push(newCage);

      const spotLight = new THREE.SpotLight(0xffffff, 0.2, 200, .1, .05, 2);
      spotLight.position.x = pos[0]
      spotLight.position.z = pos[1]
      spotLight.position.y = 300;
      spotLight.lookAt(virtualObjMesh.position)
      scene.add(spotLight);
    }
    cages.push(cage)
  }
}

const rotateCage = () => {
  if (sceneCage.rotation.x > 0)
    sceneCage.rotateX(.05)
  else action = repositionCage
}

assetLoader.load('table', 'assets/obj/table.drc', loadTable)
assetLoader.load('bell', 'assets/obj/bell.drc', loadBell)
assetLoader.load('cage', 'assets/obj/lamp-centered.drc', loadCage)

let enableAnimation = true;

let animate = function () {
  requestAnimationFrame(animate);
  if (enableAnimation) {
    action()

    if (controls.enabled) controls.update();

    renderer.render(scene, camera)
  }
};

eventManager.init(camera, renderer, null)
eventManager.setActiveTargets(activeTargets, () => {
  eventManager.resetAction()
  action = rotateCage
  controls.enabled = false
})
appLoader.load(assetLoader, animate)

const menuRef = document.getElementById('menu')
const overlay = document.getElementById('overlay')
const closeStaticBtn = document.getElementById('closeStatic')
let overlayIsLocked = false;

const toggleMenu = () => {
  const menuIsOpen = menuRef.classList.contains('open')
  const overlayIsVisible = overlay.classList.contains('open')
  menuRef.classList.toggle('open')
  if (menuIsOpen == overlayIsVisible && !overlayIsLocked) {
    overlay.classList.toggle('open')
    overlay.classList.remove('close')
  }
  enableAnimation = menuIsOpen

  dataLayer.push({
    event: "click-menu",
    menuIsOpen: menuIsOpen
  })
}

const closeMenu = () => {
  menuRef.classList.remove('open')
  enableAnimation = true
}

const openMenuContent = menuId => {
  closeMenu()
  const menuContent = document.getElementById(`${menuId}--content`)
  menuSections.map(closeMenu => {
    if (closeMenu == menuId) return
    document.getElementById(`${closeMenu}--content`).classList.remove('open')
  })
  overlayIsLocked = true
  menuContent.classList.add('open')
  closeStaticBtn.classList.add('--d-block')

  dataLayer.push({
    event: "static-page-view",
    page: menuId
  })
}

document.getElementById('menuBtn').addEventListener('click', toggleMenu)

menuRef.addEventListener('click', e => {
  e.preventDefault();
  e.stopImmediatePropagation();
  e.stopPropagation();
  toggleMenu()
})

const menuSections = ['about', 'team']

menuSections.map((menuId, index) => {
  document.getElementById(menuId).addEventListener('click', e => {
    e.stopImmediatePropagation()
    e.preventDefault()
    openMenuContent(menuId)
  })
})

overlay.addEventListener('transitionend', e => {
  if (e.propertyName == 'background-color' && !overlay.classList.contains('open')) {
    overlay.classList.add('close')
  }
})

closeStaticBtn.addEventListener('click', e => {
  menuSections.map(closeMenu => {
    document.getElementById(`${closeMenu}--content`).classList.remove('open')
  })
  overlayIsLocked = false;
  overlay.classList.remove('open')
  closeStaticBtn.classList.remove('--d-block')

  dataLayer.push({
    event: "static-page-close"
  })
})